.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root{
  --color0: #222222;
  --color1: #9A9A9A;
  --color2: #B4B4B4;
  --color3: #C2C2C2;
  --color4: #DDDDDD;
  --color5: #E4E4E4;
}

/* Styling for the navigation bar */
nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 60px;
    background-color: #333;
    overflow-x: hidden;
    transition: width 0.3s;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    background-color: var(--color1);
    font-size: 0px;
}
.nav-top{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.nav-bottom {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
}

    nav:hover {
        width: 200px;
        font-size: 30px;
    }

    nav a {
        color: var(--color0);
        text-decoration: none;
        padding: 10px;
        text-align: left;
        width: 100%;
        white-space: nowrap;
        transition: color 0.2s;
    }

        nav a:hover {
            background-color: var(--color2);
        }

.signOutBtn {
    color: var(--color0);
    background: var(--color3);
    border: none;
    cursor: pointer;
    padding: 10px;
    width: 100%;
    text-align: left;
    display: none;
}

nav:hover .signOutBtn {
    display: block;
}

.signOutBtn:hover {
    background-color: #555;
}

.main-content {
    margin-left: 60px; /* Offset by the width of the nav */
    padding: 20px;
    transition: margin-left 0.3s;
}

nav:hover + .main-content {
    margin-left: 200px; /* Adjusts content margin when nav is expanded */
}

