/* Hide scrollbar for all browsers */
.scroll-container {
  overflow-y: scroll; /* Allows scrolling */
  -ms-overflow-style: none;  /* Hides scrollbar in Internet Explorer and Edge */
  scrollbar-width: none;  /* Hides scrollbar in Firefox */
}

/* Hide scrollbar for WebKit-based browsers (Chrome, Safari) */
.scroll-container::-webkit-scrollbar {
  display: none;  /* Hides scrollbar in Chrome, Safari, and other WebKit-based browsers */
}

.itemContainer {
  position: relative; /* This makes the container a reference for the absolute position of the lock icon */
  display: inline-block;
}

.lockIcon {
  position: absolute;
  top: 10px; /* Adjust as needed */
  right: 10px; /* Adjust as needed */
  border-radius: 50%; /* Optional: Make it circular */
  padding: 5px; /* Optional: Add some padding */
  z-index: 1; /* Ensure it's above other elements */
}

