.calendarContainer {
  width: 1000px;
  display: grid;
  grid-template-columns: repeat(7, 1fr); /* 7 columns for 7 days in a week */
  gap: 5px;
  margin: auto;
}

.calendarDay {
  position: relative;
  border: 1px solid #ccc;
  padding: 10px;
  text-align: right;
  height: 100px;
  width: 100px;
  transition: border 0.3s ease, padding 0.3s ease; /* Apply transition to both border and padding */
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  overflow: hidden;
}

.calendarDay:hover {
