  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
}

.calendarDay:hover .logButton {
  display: flex;
  opacity: 0;
  transition: opacity 0.3s ease; /* Add smooth transition for button appearance */
}

.calendarDay:hover .logButton {
